import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { ToastService } from 'src/app/toast.service';
import { AuthService } from '../auth.service';

@Component({
  selector: 'app-recovery',
  templateUrl: './recovery.page.html',
  styleUrls: ['./recovery.page.scss'],
})
export class RecoveryPage implements OnInit {
  submit = false;
  form: FormGroup;
  constructor(private fb: FormBuilder, private toastService: ToastService, private dataService: AuthService) { }

  ngOnInit() {
    this.form = this.fb.group({
      rut: ['', [Validators.required, Validators.minLength(12), Validators.maxLength(12)]]
    });
  }

  recovery() {
    this.submit = true;
    console.log(this);
    if (this.form.invalid) {
      this.toastService.presentToast('Es necesario ingresar un RUT valido para continuar.');
      this.submit = false;
      return;
    } else {
      const rut = this.form.value.rut.split('.').join('');
      this.dataService.recovery(rut).subscribe(res => {
        console.log(res);
        this.toastService.presentToast(res);
      }, error => {
        this.toastService.presentToast(error);
        console.log(error);
      });
    }
  }


}
